/* Base Styles */
:root {
  --primary: #4f46e5;
  --primary-light: #6366f1;
  --primary-dark: #4338ca;
  --secondary: #10b981;
  --danger: #ef4444;
  --warning: #f59e0b;
  --info: #3b82f6;
  --light: #f9fafb;
  --dark: #111827;
  --gray: #6b7280;
  --gray-light: #e5e7eb;
  --gray-dark: #374151;
  --white: #ffffff;
  --shadow-sm: 0 1px 2px 0 rgba(0, 0, 0, 0.05);
  --shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06);
  --shadow-md: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  --shadow-lg: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
  --radius-sm: 0.25rem;
  --radius: 0.375rem;
  --radius-md: 0.5rem;
  --radius-lg: 0.75rem;
  --transition: all 0.2s ease-in-out;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
  color: var(--dark);
  background-color: var(--light);
}

/* Dashboard Layout */
.dashboard-container {
  display: flex;
  min-height: 100vh;
}

.sidebar {
  width: 280px;
  background-color: var(--white);
  border-right: 1px solid var(--gray-light);
  display: flex;
  flex-direction: column;
  padding: 1.5rem 1rem;
  position: sticky;
  top: 0;
  height: 100vh;
  overflow-y: auto;
}

.main-content {
  flex: 1;
  padding: 2rem;
  overflow-y: auto;
  background-color: var(--light);
}

/* Sidebar Styles */
.sidebar-header {
  padding-bottom: 1.5rem;
  margin-bottom: 1rem;
  border-bottom: 1px solid var(--gray-light);
}

.sidebar-header h2 {
  font-size: 1.25rem;
  font-weight: 600;
  margin-top: 0.5rem;
  color: var(--dark);
}

.logo-icon {
  font-size: 1.5rem;
  color: var(--primary);
}

.user-profile {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-top: 1.5rem;
}

.avatar {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background-color: var(--gray-light);
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.avatar img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.default-avatar {
  font-size: 1.25rem;
  color: var(--gray);
}

.username {
  font-weight: 600;
  font-size: 0.875rem;
}

.user-email {
  font-size: 0.75rem;
  color: var(--gray);
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: auto;
}

.nav-link, .new-complaint-btn {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  border-radius: var(--radius);
  font-weight: 500;
  color: var(--gray-dark);
  transition: var(--transition);
  text-decoration: none;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 0.875rem;
}

.nav-link:hover, .new-complaint-btn:hover {
  background-color: var(--gray-light);
}

.nav-link.active, .new-complaint-btn.active {
  background:  linear-gradient(135deg, #6b84ff 0%, #2f28ff 100%);
  color: var(--white);
}

.new-complaint-btn {
  background:  linear-gradient(135deg, #435eec 0%, #130db8 100%);
  color: var(--white);
  margin-bottom: 1rem;
}

.new-complaint-btn:hover {
  background-color: var(--primary-dark);
}

.logout-btn {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  padding: 0.75rem 1rem;
  border-radius: var(--radius);
  font-weight: 500;
  color: var(--danger);
  transition: var(--transition);
  text-decoration: none;
  border: none;
  background: none;
  cursor: pointer;
  font-size: 0.875rem;
  margin-top: 1rem;
  border: 1px solid var(--gray-light);
}

.logout-btn:hover {
  background-color: rgba(239, 68, 68, 0.1);
}

/* Form Container */
.form-container {
  max-width: 1000px;
  margin: 0 auto;
}

.form-header {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
}

.form-header h2 {
  font-size: 1.5rem;
  font-weight: 600;
  color: var(--dark);
}

.back-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 1rem;
  border-radius: var(--radius);
  font-weight: 500;
  color: var(--gray-dark);
  transition: var(--transition);
  text-decoration: none;
  border: none;
  background-color: var(--gray-light);
  cursor: pointer;
  font-size: 0.875rem;
}

.back-button:hover {
  background-color: #e2e8f0;
}

/* Form Grid */
.form-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1.5rem;
}

.form-section {
  grid-column: span 2;
  background-color: var(--white);
  border-radius: var(--radius-md);
  padding: 1.5rem;
  box-shadow: var(--shadow-sm);
}

.form-section h3 {
  font-size: 1.125rem;
  font-weight: 600;
  margin-bottom: 1.25rem;
  color: var(--dark);
}

.section-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}

.form-field {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.form-field.span-2 {
  grid-column: span 2;
}

.form-field label {
  font-size: 0.875rem;
  font-weight: 500;
  color: var(--gray-dark);
}

.required {
  color: var(--danger);
  margin-left: 0.25rem;
}

/* Input Styles */
input, textarea, select {
  width: 100%;
  padding: 0.625rem 0.875rem;
  border: 1px solid var(--gray-light);
  border-radius: var(--radius);
  font-size: 0.875rem;
  transition: var(--transition);
  background-color: var(--white);
}

input:focus, textarea:focus, select:focus {
  outline: none;
  border-color: var(--primary-light);
  box-shadow: 0 0 0 3px rgba(79, 70, 229, 0.1);
}

textarea {
  min-height: 120px;
  resize: vertical;
}

/* Input with Clear Button */
.input-with-clear, .select-with-clear {
  position: relative;
}

.clear-input, .clear-select {
  position: absolute;
  right: 0.75rem;
  top: 50%;
  transform: translateY(-50%);
  background: none;
  border: none;
  color: var(--gray);
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition);
  opacity: 0;
}

.clear-input:hover, .clear-select:hover {
  color: var(--danger);
  background-color: rgba(239, 68, 68, 0.1);
}

.input-with-clear:hover .clear-input,
.select-with-clear:hover .clear-select,
.clear-input:focus, .clear-select:focus {
  opacity: 1;
}

/* File Upload */
.file-upload {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.file-upload-btn {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.625rem 1.25rem;
  background-color: var(--gray-light);
  color: var(--gray-dark);
  border-radius: var(--radius);
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
  width: fit-content;
}

.file-upload-btn:hover {
  background-color: #e2e8f0;
}

.file-upload input[type="file"] {
  display: none;
}

.file-hint {
  font-size: 0.75rem;
  color: var(--gray);
}

.file-preview {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.file-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.5rem 0.75rem;
  background-color: var(--gray-light);
  border-radius: var(--radius);
  font-size: 0.875rem;
}

.file-info {
  flex: 1;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  overflow: hidden;
}

.file-name {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.file-size {
  color: var(--gray);
  font-size: 0.75rem;
  white-space: nowrap;
}

.remove-file-btn {
  background: none;
  border: none;
  color: var(--gray);
  cursor: pointer;
  padding: 0.25rem;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: var(--transition);
}

.remove-file-btn:hover {
  color: var(--danger);
  background-color: rgba(239, 68, 68, 0.1);
}

/* Form Actions */
.form-actions {
  display: flex;
  justify-content: flex-end;
  gap: 1rem;
  margin-top: 1rem;
}

.reset-btn, .submit-btn {
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius);
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.reset-btn {
  background-color: var(--gray-light);
  color: var(--gray-dark);
  border: none;
}

.reset-btn:hover {
  background-color: #e2e8f0;
}

.submit-btn {
  background:  linear-gradient(135deg, #435eec 0%, #130db8 100%);
  color: var(--white);
  border: none;
}

.submit-btn:hover {
  background-color: var(--primary-dark);
}

.submit-btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* Spinner */
.spinner {
  width: 1rem;
  height: 1rem;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: var(--white);
  animation: spin 1s ease-in-out infinite;
}

@keyframes spin {
  to { transform: rotate(360deg); }
}

.spinner-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 2rem;
}

/* Error Messages */
.alert-error {
  display: flex;
  gap: 1rem;
  padding: 1rem;
  background-color: rgba(239, 68, 68, 0.1);
  border-left: 4px solid var(--danger);
  border-radius: var(--radius-sm);
  margin-bottom: 1.5rem;
}

.alert-error h3 {
  font-size: 0.875rem;
  font-weight: 600;
  color: var(--danger);
  margin-bottom: 0.5rem;
}

.alert-error ul {
  list-style: none;
  font-size: 0.875rem;
  color: var(--gray-dark);
}

.alert-error li {
  margin-bottom: 0.25rem;
}

.error-message {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 2rem;
  text-align: center;
}

.error-message button {
  padding: 0.5rem 1rem;
  background-color: var(--gray-light);
  border: none;
  border-radius: var(--radius);
  cursor: pointer;
  transition: var(--transition);
}

.error-message button:hover {
  background-color: #e2e8f0;
}

/* History Container */
.history-container {
  max-width: 1200px;
  margin: 0 auto;
}

.history-container h1 {
  font-size: 1.75rem;
  font-weight: 600;
  margin-bottom: 1.5rem;
  color: var(--dark);
}

.search-filter {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
}

@media (min-width: 768px) {
  .search-filter {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}

.search-bar {
  position: relative;
  flex: 1;
  max-width: 400px;
}

.search-bar input {
  padding-left: 2.5rem;
  width: 100%;
}

.search-bar svg {
  position: absolute;
  left: 1rem;
  top: 50%;
  transform: translateY(-50%);
  color: var(--gray);
}

.status-filters {
  display: flex;
  gap: 0.5rem;
  flex-wrap: wrap;
}

.status-filters button {
  padding: 0.5rem 1rem;
  border-radius: var(--radius);
  font-size: 0.75rem;
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
  border: 1px solid var(--gray-light);
  background-color: var(--white);
  display: flex;
  align-items: center;
  gap: 0.25rem;
}

.status-filters button.active {
  border-color: currentColor;
  background-color: rgba(79, 70, 229, 0.1);
}

.status-filters button.all.active {
  color: var(--primary);
  border-color: var(--primary);
}

/* Complaints Grid */
.complaints-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 1.5rem;
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  padding: 3rem;
  text-align: center;
  background-color: var(--white);
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-sm);
}

.empty-state p {
  color: var(--gray);
}

/* Complaint Card */
.complaint-card {
  background-color: var(--white);
  border-radius: var(--radius-md);
  box-shadow: var(--shadow-sm);
  overflow: hidden;
  transition: var(--transition);
  border: 1px solid var(--gray-light);
}

.complaint-card:hover {
  box-shadow: var(--shadow);
}

.card-header {
  padding: 1.25rem;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  gap: 1rem;
}

.header-content {
  flex: 1;
}

.card-header h3 {
  font-size: 1rem;
  font-weight: 600;
  margin-bottom: 0.5rem;
  color: var(--dark);
}

.card-meta {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  flex-wrap: wrap;
  font-size: 0.75rem;
  color: var(--gray);
}

.complaint-id {
  font-weight: 500;
  color:#fff;
}

.expand-icon {
  color: var(--gray);
  transition: var(--transition);
}

.complaint-card.expanded .expand-icon {
  transform: rotate(180deg);
}

.card-body {
  padding: 0 1.25rem;
}

.complaint-details {
  padding: 0.5rem 0 1.25rem;
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
}

.detail-row {
  display: flex;
  gap: 0.5rem;
}

.detail-row.full-width {
  flex-direction: column;
}

.detail-label {
  font-weight: 500;
  font-size: 0.875rem;
  color: var(--gray-dark);
  min-width: 100px;
}

.detail-value {
  font-size: 0.875rem;
  color: var(--gray-dark);
  flex: 1;
}

.admin-response {
  background-color: rgba(59, 130, 246, 0.05);
  padding: 0.75rem;
  border-radius: var(--radius);
  border-left: 3px solid var(--info);
}

.attachments-list {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-top: 0.5rem;
}

.attachment-item {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.875rem;
}

.view-attachment {
  color: var(--primary);
  text-decoration: none;
  font-weight: 500;
  margin-left: auto;
}

.view-attachment:hover {
  text-decoration: underline;
}

/* Status Badge */
.status-badge {
  display: inline-flex;
  align-items: center;
  gap: 0.25rem;
  padding: 0.25rem 0.5rem;
  border-radius: var(--radius);
  font-size: 0.75rem;
  font-weight: 500;
  border: 1px solid transparent;
}

/* Success Modal */
.success-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  padding: 1rem;
}

.success-modal {
  background-color: var(--white);
  border-radius: var(--radius-md);
  padding: 2rem;
  max-width: 500px;
  width: 100%;
  text-align: center;
  box-shadow: var(--shadow-lg);
}

.success-icon {
  font-size: 3rem;
  color: var(--secondary);
  margin-bottom: 1rem;
}

.success-modal h3 {
  font-size: 1.5rem;
  font-weight: 600;
  margin-bottom: 1rem;
  color: var(--dark);
}

.success-modal p {
  color: var(--gray-dark);
  margin-bottom: 1rem;
}

.complaint-id {
  font-size: 1.25rem;
  font-weight: 600;
  color: #fff;
  margin: 1rem 0;
  padding: 0.5rem;
  background:  linear-gradient(135deg, #435eec 0%, #130db8 100%);
  border-radius: var(--radius);
  display: inline-block;
}

.modal-actions {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1.5rem;
}

.view-complaints-btn, .close-modal-btn {
  padding: 0.75rem 1.5rem;
  border-radius: var(--radius);
  font-weight: 500;
  cursor: pointer;
  transition: var(--transition);
}

.view-complaints-btn {
  background:  linear-gradient(135deg, #435eec 0%, #130db8 100%);
  color: var(--white);
  border: none;
}

.view-complaints-btn:hover {
  background-color: var(--primary-dark);
}

.close-modal-btn {
  background-color: var(--gray-light);
  color: var(--gray-dark);
  border: none;
}

.close-modal-btn:hover {
  background-color: #e2e8f0;
}

/* Responsive Adjustments */
@media (max-width: 768px) {
  .dashboard-container {
    flex-direction: column;
  }

  .sidebar {
    width: 100%;
    height: auto;
    position: relative;
    padding: 1rem;
  border-right: none;
    border-bottom: 1px solid var(--gray-light);
  }

  .main-content {
    padding: 1.5rem;
  }

  .form-grid, .section-grid {
    grid-template-columns: 1fr;
  }

  .form-field.span-2 {
    grid-column: span 1;
  }

  .complaints-grid {
    grid-template-columns: 1fr;
  }

  .search-filter {
    flex-direction: column;
  }

  .search-bar {
    max-width: 100%;
  }
}